<template>
  <div class="container">
    <div class="login_Box">
      <div class="Title">
        <h3>管理员登录</h3>
      </div>
      <!-- 登录表单 -->
      <el-form :model="Form" class="elForm" ref="FormRef" :rules="FormRules">
        <el-form-item prop="username">
          <el-input
            type="text"
            v-model.trim="Form.username"
            prefix-icon="iconfont icon-yonghuming"
            placeholder="用户名"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            type="password"
            v-model.trim="Form.password"
            prefix-icon="iconfont icon-mima"
            placeholder="密码"
          ></el-input>
        </el-form-item>
        <el-form-item class="but">
          <el-button type="primary" @click="Login">登录</el-button>
          <el-button type="info" @click="Reset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 用户登录表单信息
      Form: {
        username: '',
        password: '',
      },
      // 登录表单验
      FormRules: {
        username: [
          {
            required: true,
            trigger: 'blur',
            message: '请输入用户名',
          },
          {
            min: 2,
            max: 8,
            message: '用户名长度在2-8个字符之间',
            trigger: 'blur',
          },
        ],
        password: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur',
          },
          {
            min: 6,
            max: 18,
            message: '密码长度在6~18个字符之间',
            trigger: 'blur',
          },
        ],
      },
    }
  },
  created() {},
  methods: {
    // 登录
    Login() {
      this.$refs.FormRef.validate((valid) => {
        if (!valid) {
          return //this.$message.error('请输入用户名和密码')
        }
        this.$http.post(this.$api.API_SERVER_LOGIN, this.Form).then((res) => {
          if (res.data.status == 200) {
            if (res.data.data.status) {
              this.$message.success(res.data.msg)
              // token
              this.Storage.Session.set('token', res.data.token)
              this.Storage.Session.set('data',JSON.stringify(res.data.data))
              this.$router.push('/home')
              this.Handl.Sess(res.data)
            }else{
              return this.$message.error('当前账号可能管理员关停...')
            }
          } else {
            return this.$message.error(res.data.msg)
          }
        })
      })
    },
    // 表单重置
    Reset() {
      this.$refs.FormRef.resetFields()
    },
  },
}
</script>
<style lang='less' scoped>
.container {
  height: 100%;
  background-image: url('../assets/images/bg.jpg');
  background-size: 100% 100%;
}
.login_Box {
  position: absolute;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -35%);
  width: 450px;
  height: 280px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 0px 3px rgba(255, 255, 255);
}
.Title {
  height: 80px;
  line-height: 80px;
  text-align: center;
}
.elForm {
  margin: 0px 20px;
}
.but {
  display: flex;
  justify-content: flex-end;
}
</style>